import React, { Component } from 'react'
import Navigation from './components/navigation';
import Header from './components/header';
import Profile from './components/profile';
import Skills from './components/skills';
import Projects from './components/projects';
import Contact from './components/contact';
import JsonData from './data/data.json';

export class App extends Component {
  state = {
    landingPageData: {},
  }
  getlandingPageData() {
    this.setState({landingPageData : JsonData})
  }

  componentDidMount() {
    this.getlandingPageData();
  }

  render() {
    return (
      <div>
        <Navigation />
        <Header data={this.state.landingPageData.Header} />
        <Profile data={this.state.landingPageData.About} />
        {/* <Features data={this.state.landingPageData.Features} /> */}
        <Skills  data={this.state.landingPageData.Skill}/>
        <Projects data={this.state.landingPageData.Projects} icons={this.state.landingPageData.Icons} />
        {/* <Testimonials data={this.state.landingPageData.Testimonials} /> */}
        {/* <Team data={this.state.landingPageData.Team} /> */}
        <Contact data={this.state.landingPageData.Contact} icons={this.state.landingPageData.Icons}/>
      </div>
    )
  }
}

export default App;
